// lost
%clearfix {
	lost-utility: clearfix; }





// padding
@mixin padding16 {
	padding: 16px; }
%padding16 {
	@include padding16; }





// 圖片
@mixin img-responsive {
	display: block;
	max-width: 100%;
	height: auto; }
%img-responsive {
	@include img-responsive; }





// li inline-list
@mixin list-inline {
	list-style: none;
	li {
		display: inline-block; } }
%list-inline {
	@include list-inline; }





// 陰影
@mixin box-shadow-z1 {
	box-shadow: $z1; }
%box-shadow-z1 {
	@include box-shadow-z1; }

@mixin box-shadow-z2 {
	box-shadow: $z2; }
%box-shadow-z2 {
	@include box-shadow-z2; }

@mixin box-shadow-z3 {
	box-shadow: $z3; }
%box-shadow-z3 {
	@include box-shadow-z3; }

@mixin box-shadow-z4 {
	box-shadow: $z4; }
%box-shadow-z4 {
	@include box-shadow-z4; }





// 文字、超連結
@mixin link-hover {
	&:hover, &:focus {
		@content; } }

// 單行省略
@mixin text-ellipsis {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis; }
%text-ellipsis {
	@include text-ellipsis; }

// 多行省略
@mixin text-more-ellipsis {
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical; }
%text-more-ellipsis {
	@include text-more-ellipsis; }

// 白色文字
@mixin colorwhite {
	color: rgba(#FFF, .87); }
%colorwhite {
	@include colorwhite; }





// 常用
@mixin size($width, $height) {
	width: $width;
	height: $height; }
