// 寬螢幕
@mixin desktop-large {
	@media screen and (min-width: 1440px) {
		@content; } }

// 電腦螢幕
@mixin desktop-normal {
	@media screen and (min-width: 769px) {
		@content; } }

// 主要以ipad（直立時）為主
@mixin tablet {
	@media screen and (max-width: 768px) {
		@content; } }

// 手機以上的螢幕大小
@mixin phones-up {
	@media screen and (min-width: 540px) {
		@content; } }

// 手機螢幕
@mixin phones {
	@media screen and (max-device-width: 480px) {
		@content; } }
