body {
	@extend %clearfix;
	background-color: #F44336;
}

.refresh-wrap {
	position: absolute 0;
	z-index: -1;
	height: 100%;
	width: 100%;
	background-color: rgba(#B71C1C, .87);
	opacity: 0;
	transition: $transition-default;
	> .content {
		lost-align: center;
		height: 100%;
		width: 100%;
		color: rgba(#FFEB3B, .87);
	}
	&.-show {
		z-index: 10;
		opacity: 1;
	}
}